import { createRouter, createWebHashHistory, RouteRecordRaw } from "vue-router";
import Home from "../views/Home/index.vue";

const routes: Array<RouteRecordRaw> = [
  {
    path: "/",
    name: "Home",
    component: Home,
    meta: {
      title: "首页",
      icon: "home",
    },
  },
  {
    path: "/arctive",
    name: "Arctive",
    component: () => import("../views/Arctive/index.vue"),
    meta: {
      title: "文章",
      icon: "youxian",
    },
  },
  {
    path: "/collect",
    name: "Collect",
    component: () => import("../views/Collect/collect.vue"),
    meta: {
      title: "快捷导航",
      icon: "home",
    },
  },
  {
    path: "/components",
    name: "Components",
    component: () => import("../views/Components/index.vue"),
    meta: {
      title: "组件",
      icon: "component",
    },
    children: [
      {
        path: "svg",
        name: "Svg",
        component: () => import("../views/Components/Svg/index.vue"),
        meta: {
          title: "svg组件",
        },
      },
      {
        path: "countTo",
        name: "CountTo",
        component: () => import("../views/Components/CountTo/index.vue"),
        meta: {
          title: "CountTo",
        },
      },
      {
        path: "magnifler",
        name: "Magnifler",
        component: () => import("../views/Components/Magnifler/index.vue"),
        meta: {
          title: "Magnifler放大镜",
        },
      },
      {
        path: "waterMark",
        name: "WaterMark",
        component: () => import("../views/Components/WaterMark/index.vue"),
        meta: {
          title: "添加水印",
        },
      },
      {
        path: "loading",
        name: "Loading",
        component: () => import("../views/Components/Loading/index.vue"),
        meta: {
          title: "loading",
        },
      },
    ],
  },
  {
    path: "/notes",
    name: "Notes",
    component: () => import("../views/Notes/index.vue"),
    meta: {
      title: "大杂烩",
      icon: "note",
    },
  },
];

const router = createRouter({
  history: createWebHashHistory(),
  routes,
});

export default router;
