import axios from "axios";
import qs from "qs";
// import jsonp from "jsonp";

const http = axios.create({
  baseURL: process.env.baseURL || "",
  timeout: process.env.timeout || 20000,
  headers: {
    "Content-Type": "application/json;charset=UTF-8",
    "X-Requested-With": "XMLHttpRequest",
  },
  transformRequest: [
    function (data) {
      return data;
    },
  ],
});

// 请求拦截
http.interceptors.request.use(
  function (config: any) {
    if (config.method === "get") {
      config.paramsSerializer = function (params: any) {
        return qs.stringify(params, { arrayFormat: "repeat" });
      };
    }
    return config;
  },
  (error) => Promise.reject(error)
);

// 响应拦截
http.interceptors.response.use(
  function (response: any) {
    return response.data || response;
  },
  (error) => Promise.reject(error)
);

export default async ({
  url,
  data,
  method = "post",
}: {
  url: string;
  data?: unknown;
  method: string;
}) => {
  try {
    let response = null;
    if (method.toLocaleUpperCase() === "POST") {
      response = await http({ url, data, method });
    }
    // else if (method.toLocaleUpperCase() === "JSONP") {
    //   return new Promise((resolve) => {
    //     jsonp(url, null, (err: any, res: any) => {
    //       console.log(err, res);
    //       debugger;
    //       resolve(res);
    //     });
    //   });
    // }
    else {
      response = await http({ url, params: data, method });
    }
    return response;
  } catch (error) {
    console.log(error);
  }
};
