import { createApp } from "vue";
import App from "./App.vue";
import router from "./router";
import "@/icons";
import ElementPlus from "element-plus";
import "element-plus/dist/index.css";
import "@/assets/css/global.scss";
import "@/assets/css/fonts.scss";
import VueMarkdowmEditor from "@kangc/v-md-editor";
import "@kangc/v-md-editor/lib/style/base-editor.css";
import vuepressTheme from "@kangc/v-md-editor/lib/theme/vuepress.js";
import "@kangc/v-md-editor/lib/theme/style/vuepress.css";
import createCopyCodePlugin from "@kangc/v-md-editor/lib/plugins/copy-code/index";
import "@kangc/v-md-editor/lib/plugins/copy-code/copy-code.css";
import axios from "@/utils/request";
import { createPinia } from "pinia";
import Prism from "prismjs";
import "vue-fullpage.js/dist/style.css";
import VueFullPage from "vue-fullpage.js";
import { searchStore } from "@/store/search";
import SvgIcon from "@/components/SvgIcon/index.vue";

VueMarkdowmEditor.use(vuepressTheme, {
  Prism,
}).use(createCopyCodePlugin());

const app = createApp(App);
app.config.globalProperties.$axios = axios;
app.component("svg-icon", SvgIcon);
app
  .use(router)
  .use(ElementPlus)
  .use(VueMarkdowmEditor)
  .use(createPinia())
  .use(VueFullPage)
  .mount("#app");

/**
 * 路由钩子
 * home页面使用了vue-fullpage.js，会默认给父级main标签增加height：100%；
 * 从路由 / 跳转到其他页面，将main标签height样式设置为空，使其不生效。
 */
const store = searchStore();
router.beforeEach((to, from, next) => {
  store.searchValue = "";
  store.searchVisible = to.path === "/arctive";

  if (from.path !== to.path && from.path === "/") {
    document.getElementsByTagName("main")[0].style.height = "";
  }
  next();
});
