import request from "./request";
/**
 * 360随机壁纸 - 随机分类，随机起始位查询99条数据，取随机一条返回
 * @cid 类别id   1:每日精选  5:游戏  6:美女  9:风景  10:视觉创意   11:明星影视  12:汽车  14:萌宠动物   15:小清新  16:体育  22:军事  26:动漫卡通  30:情感  35:文字
 */
interface responseState {
  consume?: string;
  data: [];
  errno?: string;
  errmsg?: string;
  total?: string;
}
export const getWallpaper = async () => {
  const cidList: number[] = [
    1, 5, 6, 9, 10, 11, 12, 14, 15, 16, 22, 26, 30, 35,
  ];
  const index: number = Math.floor(Math.random() * cidList.length);
  const cid: number = cidList[index];
  const start: number = Math.floor(Math.random() * 100); // 起始位置
  const isDev = process.env.NODE_ENV === "development";

  const url = `${
    isDev ? "/wallpaper" : "http://wallpaper.apc.360.cn/index.php"
  }?c=WallPaperAndroid&a=getAppsByCategory&cid=${cid}&start=${start}&count=99`;
  return new Promise((resolve, reject) => {
    request({
      url,
      method: "get",
    }).then((res) => {
      const { errno, data } = res as responseState;
      if (errno === "0") {
        const len = data.length;
        const random = Math.floor(Math.random() * len);
        resolve(data[random]);
      }
      reject(null);
    });
  });
};

/**
 * 添加水印
 */
export const waterMark = (dom: any, interval = 200, angle = -45) => {
  // 创建一个Canvas元素
  const canvas = document.createElement("canvas");
  canvas.classList.add("water-mark");
  dom.appendChild(canvas);
  // 设置Canvas的样式和大小
  canvas.style.position = "fixed";
  canvas.style.pointerEvents = "none";
  canvas.style.top = dom?.offsetTop || 0;
  canvas.style.left = dom?.offsetLeft || 0;
  canvas.width = dom.offsetWidth || window.innerWidth;
  canvas.height = dom.offsetHeight || window.innerHeight;

  // 获取Canvas的2D上下文
  const ctx = canvas.getContext("2d") as CanvasRenderingContext2D;

  // 设置水印文本的样式
  const text = "Watermark";
  ctx.font = "20px Arial";
  ctx.fillStyle = "rgba(0, 0, 0, 0.1)";
  ctx.textAlign = "center";
  ctx.textBaseline = "middle";

  // 定义水印间距和倾斜角度
  // const interval = 100; // 水印间距
  // const angle = -45; // 倾斜角度

  // 循环生成水印
  for (let x = 0; x < canvas.width; x += interval) {
    for (let y = 0; y < canvas.height; y += interval) {
      // 保存Canvas的初始状态
      ctx.save();
      // 平移Canvas的原点到当前位置
      ctx.translate(x, y);
      // 倾斜Canvas
      ctx.rotate((angle * Math.PI) / 180);
      // 在Canvas上绘制倾斜的水印文本
      ctx.fillText(text, 0, 0);
      // 恢复Canvas的状态
      ctx.restore();
    }
  }
};

/**
 * AES加密
 */
export const aesEncrypt = (text: string) => {
  const key = window.CryptoJS.enc.Utf8.parse("aes-password");
  const str = window.CryptoJS.enc.Utf8.parse(text);
  const encrypted = window.CryptoJS.AES.encrypt(str, key, {
    mode: window.CryptoJS.mode.ECB,
    padding: window.CryptoJS.pad.Pkcs7,
  });
  return encrypted.toString();
};

/**
 * AES解密
 */
export const aesDecrypt = (text: string) => {
  const key = window.CryptoJS.enc.Utf8.parse("aes-password");
  const decrypt = window.CryptoJS.AES.decrypt(text, key, {
    mode: window.CryptoJS.mode.ECB,
    padding: window.CryptoJS.pad.Pkcs7,
  });
  return window.CryptoJS.enc.Utf8.stringify(decrypt).toString();
};
